/* =========================== Quad Boxes ====================*/

article {
    position: relative;
    div[id="quadrants"] {
        position: relative;
        max-width: 1400px;
        margin: 0 auto;
        div[class^="col-"] {
            position: relative;
            min-height: 100%;
            padding: 4rem 10%;
            background-position: center center !important;
            border-bottom: 1px solid var(--wgo);
            display: flex; // flex: 1;
            max-width: unset;
            flex-direction: column;
            justify-content: center; // this centers smaller quad vertically
            .boxa {
                p {
                    font-size: 1.2rem;
                }
            }
            @media screen and (min-width: 768px) {
                padding: 4rem 5%;
                &:nth-of-type(2n-1) {
                    border-right: 1px solid var(--wgo);
                }
            }
        }
    }
}

main {
    section,
    .alert-bar {
        position: relative;
        padding: 4rem 10%;
        @media screen and (min-width: 1140px) {
            padding: 2rem 5%;
        }
        .center-shell {
            position: relative;
            max-width: calc(1400px - 8%);
            margin: 0 auto;
        }
    }
}


/* =============================================== Q: Text/General ==================*/

p,
li,
td {
    color: var(--graydk);
    line-height: 1.6;
}

.general,
.boxa,
.boxb {
    h1,
    h3,
    blockquote p {
        color: var(--ssgr);
    }
    h2 {
        padding: 1rem 0 0;
    }
    a {
        &:not(.button-default) {
            color: var(--stgr);
            text-decoration: underline;
            &:hover,
            &:focus {
                color: var(--ssgr);
                text-decoration: none;
            }
            &:visited {
                color: var(--graymd);
            }
        }
    }
    strong {
        font-weight: 500;
    }
    img {
        width: 100%;
        object-fit: contain;
        margin: 1rem 0;
        border-radius: 4px;
        &.small-image-right {
            float: right;
            width: unset;
            max-width: 25ch;
            margin: 0 0 1rem 2em;
            border: 0 !important;
        }
    }
}

