// =============================================== Q: image / video 
.image,
[class^=image] {
    padding: 0;
    background-position: center;
    background-repeat: no-repeat;
    min-height: 300px;
    * {
        display: none !important;
    }
    &.bg-logo {
        //        padding: 2em 10%;
        //        background-color: #fff;
    }
    @media screen and (max-width: 768px) {
        min-height: 320px;
    }
    @media screen and (min-width: 640px) {
        min-height: 480px
    }
}

.video,
[class^=video] {
    h2 {
        text-indent: -9999px;
        height: 0;
        overflow: hidden;
        margin: 0;
        padding: 0;
    }
}

// =============================================== Q: showcase 
.showcase {
    /* outer block */
    padding: 5%;
    display: flex;
    flex-direction: column;
    justify-content: left;
    background-color: #043124;
    background-size: contain;
    background-repeat: no-repeat;
    h2 {
        /*font-size:22px;  dupe 322 ?*/
        color: #fac606;
        text-indent: -9999px;
        height: 0;
        margin: 0;
        &:first-of-type {
            text-indent: 0;
            color: #d9d0ac;
            align-self: flex-start;
            flex: 1 1 25%;
            border-radius: 0;
            background: rgba(0, 0, 0, .75);
            text-shadow: 0 2px 3px rgba(0, 0, 0, .75);
            background: -webkit-gradient(linear, left bottom, left top, from(rgba(0, 0, 0, 0)), color-stop(25%, rgba(0, 0, 0, 0)), color-stop(75%, rgba(0, 0, 0, .75)), to(rgba(0, 0, 0, 1)));
            background: linear-gradient(0deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 25%, rgba(0, 0, 0, .75) 75%, rgba(0, 0, 0, 1) 100%);
            padding: 1.5em 15% 1em;
            width: 100%;
            height: auto;
            @media screen and (min-width: 992px) {
                padding: 1.5em 10% 1em;
            }
        }
    }
}


